import freeAvater from'@/components/free-avater.vue'
import uniIcon from '@/components/uni-icon/uni-icon.vue'
import divider from '@/components/divider.vue';
import $U from '@/common/util.js';
export default {
	
	components:{
		divider,
		freeAvater
	},
	
	data() {
		return {
			background:{
				backgroundColor: '#fff',
			},
			yongjin:0,
			ceHuaShow:false,
			userName:""
		}
	},
	
	onLoad() {
		this.userName=$U.getStorage("userInfo").nickName
	},
	
	methods: {
		
		toBuy(){
			console.log(this.userName)
			uni.navigateTo({
				url:'../gouMaiFuWu/gouMaiFuWu?name='+this.userName
			})
			
			
		},
		
		toDianPuZiLiao(){
			uni.navigateTo({
				url:'../dianPuZiLiao/dianPuZiLiao'
			})
		},
		toYongJin(){
			uni.navigateTo({
				url:'../yongJinPeiZhi/yongJinPeiZhi'
			})
		},
		
		toShouKuanMa(){
			uni.navigateTo({
				url:'../shouKuanMa/shouKuanMa'
			})
		},
		
		toYaoYue(){
			uni.navigateTo({
				url:'../tuiJianYaoYue/tuiJianYaoYue'
			})
		},
		
		toShop(){
			uni.navigateTo({
				url:'../../shop/shop/shop'
			})
		},
		
		showCeHuaShi(){
			this.ceHuaShow=true
		}
	}
}
